import React from 'react';
import { HashRouter, Redirect, Route, Switch } from 'react-router-dom';
import Applications from './Applications';
import Authenticators from './Authenticators';
import AccountDetails from './Details/AccountDetails';
import EpmDetails from './Details/EpmDetails';
import Home from './Home';
import PasswordGenerator from './PasswordGenerator';
import PasswordManager from './PasswordManager';
import RouteNotFound from './RouteNotFound';
import SharedAccounts from './SharedAccounts';
import './style.css';

const Routes = () => (
  <HashRouter>
    <Route>
      <Switch>
        <Redirect exact from="/" to="/home" />
        <Route path="/home" component={Home} />
        <Route path="/applications" component={Applications} />
        <Route path="/authenticators" component={Authenticators} />
        <Route path="/password-manager" component={PasswordManager} />
        <Route path="/shared-accounts" component={SharedAccounts} />
        <Route path="/epm-details/:appId/:accId" component={EpmDetails} />
        <Route path="/account-details/:id" component={AccountDetails} />
        <Route path="/password-generator" component={PasswordGenerator} />
        <Route component={RouteNotFound} />
      </Switch>
    </Route>
  </HashRouter>
);

export default Routes;
