import React, { Component } from 'react';
import Footer from '../../Footer';
import Nav from '../../Nav';
import '../../popup.css';
import objectHasSubstring from '../../util/objectHasSubstring';
import './login-items.css';
import LoginItemList from './LoginItemList';

class LoginItemPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filter: '',
    };
  }

  render() {
    return (
      <div>
        <Nav back sync />
        <main className="main">
          <ol className="breadcrumb login-items__breadcrumb">
            <li className="active login-items__active">{this.props.title}</li>
          </ol>
          {this.props.items.length > 8 ? (
            <div className="form-group login-items__form-group list-item__border-bottom">
              <input
                autoFocus
                className="form-control popup__search-input"
                onChange={e => this.setState({ filter: e.target.value })}
                placeholder="Search"
              />
              <span className="glyphicon glyphicon-search login-items__form-control-glyphicon color-summer-sky" />
            </div>
          ) : null}
          {this.props.items.length ? (
            <LoginItemList
              items={this.props.items.filter(
                objectHasSubstring(this.state.filter),
              )}
            />
          ) : (
            <p className="login-items__result">No items.</p>
          )}
        </main>
        <Footer />
      </div>
    );
  }
}

export default LoginItemPage;
