import { Subject } from 'rxjs';
import { Stomp } from 'stompjs/lib/stomp';
import sessionId from './sessionId';

let client = null;
let connecting = false;
const messages$ = new Subject();

const connect = () => {
  if ((client && client.connected) || connecting) {
    return;
  }

  const { url, username, password, virtualHost } = api.websocket;

  client = Stomp.client(url);
  client.debug = null;
  connecting = true;

  client.connect(
    username,
    password,
    onConnect,
    onError,
    virtualHost,
  );
};

const onConnect = () => {
  connecting = false;
  client.subscribe(`/exchange/websocket-exchange/${sessionId}`, onReceive);
};

const onError = () => (connecting = false);

const onReceive = frame => messages$.next(JSON.parse(frame.body));

export const subscribe = (next, error, complete) => {
  connect();
  return messages$.subscribe(next, error, complete);
};
