import React, { Component } from 'react';
import { reloadUserData } from '../../common/runInBackground';
import orca48 from './orca-48.png';
import './style.css';
import sync from './sync.png';

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };
  }

  async sync() {
    this.setState({ isLoading: true });
    await reloadUserData();
    this.setState({ isLoading: false });
  }

  render() {
    return (
      <nav className="nav">
        <p className="nav__p">
          {this.props.logo ? (
            <>
              <img src={orca48} />
              <span className="nav__middle">SAASPASS</span>
            </>
          ) : null}

          {this.props.back ? (
            <button
              className="btn btn-default navbar-btn pull-left nav__btn"
              onClick={() => window.history.back()}
            >
              <span className="glyphicon glyphicon-chevron-left" />
              Back
            </button>
          ) : null}

          {this.props.sync ? (
            <button
              className="btn btn-default navbar-btn pull-right nav__btn"
              title="Synchronize"
              disabled={this.state.isLoading}
              onClick={() => this.sync()}
            >
              <img
                src={sync}
                className={`nav__sync-img ${
                  this.state.isLoading ? 'spin' : ''
                }`}
              />
            </button>
          ) : null}
        </p>
      </nav>
    );
  }
}

export default Nav;
