import 'bootstrap/dist/css/bootstrap.css';
import React, { Component } from 'react';
import { observe } from '../common/store';
import Alerts from './Alerts';
import './App.css';
import Login from './Login';
import Routes from './Routes';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoggedIn: false,
      isStoreLoaded: false,
    };
  }

  componentDidMount() {
    this.subscription = observe('authenticators', 'passwordmanagers').subscribe(
      ({ authenticators, passwordmanagers }) =>
        this.setState({
          isLoggedIn: !!authenticators && !!passwordmanagers,
          isStoreLoaded: true,
        }),
    );
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  render() {
    return this.state.isStoreLoaded ? (
      <div>
        {this.state.isLoggedIn ? <Routes /> : <Login />}
        <Alerts />
      </div>
    ) : null;
  }
}

export default App;
