import React, { Component } from 'react';
import sortAuthenticators from '../../../common/sortAuthenticators';
import { observe } from '../../../common/store';
import LoginItemPage from '../LoginItems/LoginItemPage';

class SharedAccounts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
    };
  }

  componentDidMount() {
    this.subscription = observe('authenticators', 'passwordmanagers').subscribe(
      ({ authenticators, passwordmanagers }) =>
        this.setState({
          items: !(authenticators && passwordmanagers)
            ? []
            : sortAuthenticators(
                [...authenticators, ...passwordmanagers].filter(
                  i => i.isShared,
                ),
              ),
        }),
    );
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  render() {
    return (
      <LoginItemPage
        items={this.state.items}
        title={`Shared Accounts (${this.state.items.length})`}
      />
    );
  }
}

export default SharedAccounts;
