import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import authenticatorLogoUrl from '../../../common/authenticatorLogoUrl';
import delay from '../../../common/delay';
import normalizeItem from '../../../common/normalizeItem';
import onLogoError from '../../../common/onLogoError';
import { autoFillInit, getCredentials } from '../../../common/runInBackground';
import { observe } from '../../../common/store';
import Footer from '../../Footer';
import Nav from '../../Nav';
import copy from './copy.png';
import eye from './eye.png';
import launch from './launch.png';
import './style.css';

class AccountDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isCopiedVisible: false,
      isPasswordVisible: false,
      item: null,
      otp: '',
      password: '',
    };
  }

  componentDidMount() {
    this.subscription = observe('authenticators', 'passwordmanagers').subscribe(
      ({ authenticators, passwordmanagers }) => {
        if (!authenticators || !passwordmanagers) return;
        const { id } = this.props.match.params;
        const accounts = [...authenticators, ...passwordmanagers];
        const item = normalizeItem(accounts.find(i => i.id === id));
        this.setState({ item: item });
        this.setOtp(item);
      },
    );
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  async copyToClipboard(value) {
    await navigator.clipboard.writeText(value);
    this.setState({ isCopiedVisible: true });
    await delay(2000);
    this.setState({ isCopiedVisible: false });
  }

  async setOtp(item) {
    if (item && item.showOtp && !this.state.otp) {
      const otp = await getCredentials('OTP', item.id, item.isShared);
      this.setState({ otp });
    }
  }

  async passwordToggleVisibility() {
    if (!this.state.password) {
      const { id, isShared } = this.state.item;
      const password = `${await getCredentials('PASSWORD', id, isShared)}`;
      this.setState({ password });
    }
    this.setState(state => ({ isPasswordVisible: !state.isPasswordVisible }));
  }

  async passwordCopyToClipboard() {
    if (this.state.password) {
      await this.copyToClipboard(this.state.password);
    } else {
      const { id, isShared } = this.state.item;
      const password = `${await getCredentials('PASSWORD', id, isShared)}`;
      this.setState({ password });
      await this.copyToClipboard(password);
    }
  }

  render() {
    const item = this.state.item;
    if (!item) return null;
    const { serviceName, serviceUrl, slug } = item.publicService;
    return (
      <div>
        <Nav back />
        <main className="main">
          <ul className="list-group details__items">
            <li className="list-group-item details__item details__name">
              <div className="row">
                <div className="col-xs-3" align="center">
                  <img
                    src={authenticatorLogoUrl(slug, 64)}
                    onError={onLogoError}
                    className="details__logo"
                  />
                </div>
                <div className="col-xs-9">Service: {serviceName}</div>
                {serviceUrl ? (
                  <div className="col-xs-9">URL: {serviceUrl}</div>
                ) : null}
              </div>
            </li>
            <li className="list-group-item details__item">
              <div className="row">
                <div className="col-xs-3 details__border-right">
                  <p className="details__label">Username:</p>
                </div>
                <div className="col-xs-6">
                  <input value={item.username} readOnly />
                </div>
                <div className="col-xs-3">
                  <img
                    src={copy}
                    onClick={() => this.copyToClipboard(item.username)}
                    title="Copy"
                    className="details__icon-button"
                    align="right"
                  />
                </div>
              </div>
            </li>
            {item.showPassword ? (
              <li className="list-group-item details__item">
                <div className="row">
                  <div className="col-xs-3 details__border-right">
                    <p className="details__label">Password:</p>
                  </div>
                  <div className="col-xs-6">
                    {this.state.isPasswordVisible ? (
                      <input value={this.state.password} readOnly />
                    ) : (
                      <input type="password" value="***************" readOnly />
                    )}
                  </div>
                  <div className="col-xs-3">
                    <img
                      src={eye}
                      title="Show/hide password"
                      className="details__icon-button"
                      align="left"
                      onClick={() => this.passwordToggleVisibility()}
                    />
                    <img
                      src={copy}
                      onClick={() => this.passwordCopyToClipboard()}
                      title="Copy"
                      className="details__icon-button"
                      align="right"
                    />
                  </div>
                </div>
              </li>
            ) : null}
            {this.state.otp ? (
              <li className="list-group-item details__item">
                <div className="row">
                  <div className="col-xs-3 details__border-right">
                    <p className="details__label">OTP:</p>
                  </div>
                  <div className="col-xs-6">
                    <input value={this.state.otp} readOnly />
                  </div>
                  <div className="col-xs-3">
                    <img
                      src={copy}
                      onClick={() => this.copyToClipboard(this.state.otp)}
                      title="Copy"
                      className="details__icon-button"
                      align="right"
                    />
                  </div>
                </div>
              </li>
            ) : null}
          </ul>
          <button
            className="btn btn-primary btn-lg details__login-button"
            disabled={!item.ssoEnabled}
            onClick={() => autoFillInit({ item, newTab: true, submit: true })}
          >
            <img src={launch} />
            <span> LAUNCH</span>
          </button>
          {this.state.isCopiedVisible ? (
            <div className="details__copied">
              <span className="label label-success details__copied-label">
                <i className="glyphicon glyphicon-ok-circle details__copied-icon" />
                <span> Copied</span>
              </span>
            </div>
          ) : null}
        </main>
        <Footer />
      </div>
    );
  }
}

export default withRouter(AccountDetails);
