import React, { Component } from 'react';
import { version } from '../../../package.json';
import {
  extensionLogin,
  extensionPushLogin,
  getBarcodeImage,
} from '../../common/runInBackground';
import Footer from '../Footer';
import Nav from '../Nav';
import sessionId from '../sessionId';
import { subscribe } from '../websocket';
import barcodePlaceholder from './barcode-placeholder.png';
import scan from './scan.png';
import './style.css';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      alert: null,
      barcodeImage: barcodePlaceholder,
      isLoading: false,
      isRememberMeLoaded: false,
      password: '',
      rememberMe: false,
      username: '',
    };
  }

  async componentDidMount() {
    this._isMounted = true; // todo: remove workaround
    this.handleWebsocketMessage();

    const { username } = await browser.storage.local.get();
    this.setState({
      isRememberMeLoaded: true,
      rememberMe: !!username,
      username: username || '',
    });

    // todo: this request shouldn't start if already logged in.
    const barcodeImage = await getBarcodeImage(sessionId);
    this._isMounted && this.setState({ barcodeImage });
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.subscription.unsubscribe();
  }

  handleWebsocketMessage() {
    this.subscription = subscribe(({ type, content }) => {
      if (type === 'LOGIN') {
        this.login(content.username, content.password);
      }
    });
  }

  async login(username, password) {
    this.setState({ isLoading: true, alert: null });

    try {
      await extensionLogin(username, password);
      if (this.state.rememberMe) {
        browser.storage.local.set({ username: this.state.username });
      }
    } catch (e) {
      const { error_description = '' } = (e.response || {}).data || {};
      const message = error_description.includes('captcha')
        ? 'Too many invalid login attempts. Go to www.saaspass.com and attempt to login there.'
        : 'Incorrect credentials. If problem continues, check time/zone of your mobile device.';
      this.setState({ isLoading: false, alert: { type: 'danger', message } });
    }
  }

  async onPush() {
    this.setState({ isLoading: true, alert: null });
    try {
      await extensionPushLogin(this.state.username, sessionId);
      const message = 'Approval message has been sent to your SAASPASS app.';
      this.setState({ alert: { type: 'info', message } });
    } catch (e) {
      const data = (e.response || {}).data || {};
      const message = !data.message
        ? 'Push login failed'
        : data.message.includes('captcha')
        ? 'Too many invalid login attempts. Go to www.saaspass.com and attempt to login there.'
        : data.message;
      this.setState({ alert: { type: 'danger', message } });
    } finally {
      this.setState({ isLoading: false });
    }
  }

  onSubmit(event) {
    event.preventDefault();
    this.login(this.state.username, this.state.password);
  }

  rememberMeChanged(event) {
    const rememberMe = event.target.checked;
    this.setState({ rememberMe });
    if (!rememberMe) {
      browser.storage.local.remove('username');
    }
  }

  render() {
    return this.state.isRememberMeLoaded ? (
      <div>
        <Nav logo />
        <main className="main">
          <div className="login">
            <form className="login__form" onSubmit={e => this.onSubmit(e)}>
              <div>
                <p className="login__scan-text">Scan with SAASPASS</p>
                <img
                  src={this.state.barcodeImage}
                  className="center-block login__barcode-image"
                />
                <img src={scan} className="center-block login__scan-icon" />
              </div>

              <div className="form-group login__form-group">
                <div className="input-group">
                  <input
                    autoComplete="off"
                    autoFocus={!this.state.rememberMe}
                    className="form-control login__form-control login__input"
                    disabled={this.state.isLoading}
                    maxLength={125}
                    name="username"
                    onChange={e => this.setState({ username: e.target.value })}
                    placeholder="SAASPASS ID or email or mobile number"
                    required
                    value={this.state.username}
                  />
                  <span className="input-group-btn">
                    <button
                      type="button"
                      className="btn btn-primary"
                      disabled={!this.state.username || this.state.isLoading}
                      onClick={() => this.onPush()}
                    >
                      Push Login
                    </button>
                  </span>
                </div>
              </div>
              <div className="checkbox login__remember-me-checkbox-wrapper">
                <input
                  checked={this.state.rememberMe}
                  className="login__remember-me-checkbox"
                  id="rememberMe"
                  name="rememberMe"
                  onChange={e => this.rememberMeChanged(e)}
                  type="checkbox"
                />
                <label
                  htmlFor="rememberMe"
                  className="login__remember-me-label"
                >
                  <span className="login__remember-me-text">Remember me</span>
                </label>
              </div>
              <div className="form-group login__form-group">
                <input
                  autoComplete="off"
                  autoFocus={this.state.rememberMe}
                  className="form-control login__form-control login__input"
                  disabled={this.state.isLoading}
                  maxLength={6}
                  minLength={6}
                  name="password"
                  onChange={e =>
                    /^[0-9]{0,6}$/.test(e.target.value) &&
                    this.setState({ password: e.target.value })
                  }
                  placeholder="One-Time Password"
                  required
                  value={this.state.password}
                />
              </div>

              <button
                className="btn btn-default center-block login__button"
                disabled={
                  !this.state.username ||
                  this.state.password.length < 6 ||
                  this.state.isLoading
                }
              >
                Login
              </button>
            </form>
            {this.state.alert ? (
              <h5 className={`login__alert bg-${this.state.alert.type}`}>
                {this.state.alert.message}
              </h5>
            ) : null}
          </div>
          <h5 className="login__version">version {version}</h5>
        </main>
        <Footer />
      </div>
    ) : null;
  }
}

export default Login;
