import React, { Component } from 'react';
import sortAuthenticators from '../../../common/sortAuthenticators';
import { observe } from '../../../common/store';
import LoginItemPage from '../LoginItems/LoginItemPage';

class PasswordManager extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
    };
  }

  componentDidMount() {
    this.subscription = observe('passwordmanagers').subscribe(
      ({ passwordmanagers }) =>
        this.setState({
          items: sortAuthenticators(
            (passwordmanagers || []).filter(i => !i.isShared),
          ),
        }),
    );
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  render() {
    return (
      <LoginItemPage
        items={this.state.items}
        title={`Password Manager (${this.state.items.length})`}
      />
    );
  }
}

export default PasswordManager;
