import React, { Component } from 'react';
import sortBy from '../../../common/sortBy';
import { observe } from '../../../common/store';
import LoginItemPage from '../LoginItems/LoginItemPage';

class Applications extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
    };
  }

  componentDidMount() {
    this.subscription = observe('accounts').subscribe(({ accounts }) =>
      this.setState({
        items: accounts
          ? sortBy(
              i => !i.ssoEnabled,
              i => i.applicationTypeName.toLowerCase(),
              i => i.applicationName.toLowerCase(),
              i => i.username.toLowerCase(),
            )(accounts)
          : [],
      }),
    );
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  render() {
    return (
      <LoginItemPage
        items={this.state.items}
        title={`Company Applications (${this.state.items.length})`}
      />
    );
  }
}

export default Applications;
