import React from 'react';
import { Link } from 'react-router-dom';
import normalizeItem from '../../../common/normalizeItem';
import onLogoError from '../../../common/onLogoError';
import {
  autoFillInit,
  loginWithApplication,
} from '../../../common/runInBackground';
import login from '../../../images/login.png';
import '../../popup.css';
import './login-items.css';

const launch = item => {
  if (!item.applicationId || item.epmAppSettings) {
    autoFillInit({ item, newTab: true, submit: true });
  } else {
    loginWithApplication(item.applicationId, item.companyUserAccountId);
  }
};

const LoginItemList = ({ items }) => (
  <div className="list-group login-items__list-group login-items__scroll">
    {items.map(normalizeItem).map(item => (
      <span
        className="list-group-item popup__list-item list-item__border-bottom"
        key={item.id}
        onClick={() => item.ssoEnabled && launch(item)}
        title={item.applicationTypeName}
      >
        <img
          src={item.logo}
          className="login-items__icon pull-left"
          onError={onLogoError}
        />
        <p className="login-items__label">
          <b>{item.displayName}</b>
          <br />
          {item.username}
          {item.detailPath ? (
            <Link
              to={item.detailPath}
              onClick={e => e.stopPropagation()}
              className="glyphicon glyphicon-chevron-right pull-right login-items__more-details"
              title="More details"
            />
          ) : null}
        </p>
        {item.ssoEnabled ? (
          <img
            src={login}
            className="login-items__open login-items__img"
            title="Log in"
          />
        ) : null}
      </span>
    ))}
  </div>
);

export default LoginItemList;
