const findAllStringsAndNumbers = obj =>
  Object.values(obj)
    .filter(v => ['number', 'string'].includes(typeof v))
    .concat(
      ...Object.values(obj)
        .filter(v => v && typeof v === 'object')
        .map(findAllStringsAndNumbers),
    );

const objectHasSubstring = str => obj =>
  findAllStringsAndNumbers(obj)
    .map(v => `${v}`.toLowerCase())
    .some(s => s.includes(str.toLowerCase()));

export default objectHasSubstring;
