import React, { Component } from 'react';
import { observe } from '../../common/store';
import orcaLoader from '../../images/orca-loader.gif';
import success from './confirm.png';
import './style.css';

class Alerts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showLoading: 0,
      showSuccess: false, // todo: implement
    };
  }

  componentDidMount() {
    this.unsubscribe = observe('activeRequests').subscribe(
      ({ activeRequests }) => this.setState({ showLoading: !!activeRequests }),
    );
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  render() {
    return (
      <div>
        {this.state.showLoading ? (
          <img className="alert__loading" src={orcaLoader} />
        ) : null}
        {this.state.showSuccess ? (
          <img className="alert__success" src={success} />
        ) : null}
      </div>
    );
  }
}

export default Alerts;
