import port from './port';

const proxy = name => (...args) =>
  new Promise((resolve, reject) => {
    port.onMessage.addListener(({ type, name: n, data }) => {
      if (n !== name) {
        return;
      }
      if (type === 'proxy-success') {
        resolve(data);
      } else if (type === 'proxy-error') {
        reject(data);
      }
    });
    port.postMessage({ type: 'proxy-request', name, args });
  });

export default proxy;
